import React from 'react';
import { object } from 'prop-types';
import { inject, observer } from 'mobx-react';
import styled from '@emotion/styled';

const PreviewStyled = styled.div`

    height:311px;
  .canvas {
    margin: auto;
    width: 940px;



    padding: 0;

    border: none;
  }

  .text-canvas {
    transform: translateY(calc(-100% - 4px));
  }
`;

export const Preview = ({ baseCanvas, textCanvas }) => (
  <PreviewStyled>
    <img
      className="canvas image-canvas"
      alt="Preview"
      src={baseCanvas && baseCanvas.toDataURL('image/png')}
    />
    <img
      className="canvas text-canvas"
      alt="Preview"
      src={textCanvas && textCanvas.toDataURL('image/png')}
    />
  </PreviewStyled>
);

Preview.propTypes = {
  baseCanvas: object,
  textCanvas: object,
};

Preview.defaultProps = {
  baseCanvas: null,
  textCanvas: null,
};

export default inject(({ appState }) => ({
  baseCanvas: appState.baseCanvas,
  textCanvas: appState.textCanvas,
}))(observer(Preview));
